.Story__user {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 10px;
	font-size: 12px;
	cursor: pointer;
}
.Story__user-avatar{
	marginBottom: 8px;
	border: 2px solid #479ee0;
	border-radius: 50%;
	margin-bottom: 8px;
}

.Story__is-viewed {
	border: 2px solid #91a2af;
}
.Stories__wrapper {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
	text-align: center;
}
.ScreenSpinner__container {
	background-color: #e7e7e7 !important;
}

.ScreenSpinner__disabled{
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: #ffffff85;
	z-index: 99;
}
